/** @format */

import Loading from "@/components/Loading";
import Navbar from "@/components/Navbar";
import { useUser } from "@clerk/nextjs";
import { usePathname } from "next/navigation";
import { useState } from "react";

export default function DashboardLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const pathname = usePathname();
    const [courseId, seCourseId] = useState<string | null>(null);
    const { user, isLoaded } = useUser();

    // handle use effect isCoursePage
    if (!isLoaded) return <Loading />;
    if (!user)
        return (
            <div className="text 3xl text-center">
                Please sign in to access this page
            </div>
        );

    return (
        <div className="min-h-screen w-full bg-customgreys-primarybg flex">
            <main className="flex flex-grow w-full h-full justify-center items-center">
                {children}
            </main>
        </div>
    );
}
